import {   Stack, Typography, Link } from "@mui/material";
import { makeStyles } from "@mui/styles";

const useStyles = makeStyles({
    root: {
        backgroundColor: "#570349",
        textAlign: "center",
        padding: '2rem 0',
        
        color: "white",
    },
    item1: {
        marginBottom: '2rem'
    }
});

const Footer = () => {
    const classes = useStyles();

    return (
        <Stack className={classes.root}>
            <Stack direction='row' gap={4} justifyContent='center' className={classes.item1}>
                <Typography>UyDung</Typography>
                <Link href='mailto: dungnguyendinh911@gmail.com' underline='hover'>dungnguyendinh911@gmail.com</Link>
                <Typography>+84 398 645 078</Typography>
            </Stack>

            <Stack>
                <Typography>Copyright &copy; 2021</Typography>
            </Stack>
        </Stack>
    );
};

export default Footer;
